.sidebar {
    position: fixed;
    top: 128px;
    right: 0;
    background: #fff;
    ul {
        padding: 10px 8px;
        >li {
            padding: 5px 0;
            a {
                display: blockOld;
                width: 38px;
                text-align: center;
                color: #5e5e5e;
                .icon {
                    height: 30px;
                    color: $default-yellow-light;
                    font-size: 26px;
                    overflow: hidden;
                }
            }
            &.line {
                border-bottom: 1px dashed $border-color-default
            }
        }
    }
}
