.footer {
    background: #68624e;
    color: #fff;
    .wrap {
        padding: 20px 50px;
        background: #68624e;
    }
    .footer-t {
        padding: 0 10px;
        border-bottom: 1px dashed #7f7968;
        ul {
            margin: 15px 0 10px 0;
            @include clearfix();
            >li {
                float: left;
                margin: 0 0 10px 85px;
                a {
                    color: #bdbbb5;
                }
                &:first-child {
                    margin-left: 0;
                }
            }
        }
    }
    .footer-b {
        padding: 18px 10px 0 10px;
        @include clearfix();
        .footer-b-l {
            float: left;
            >li {
                display: inline-blockOld;
                margin-right: 25px;
                a {
                    color: #fff;
                }
            }
        }
        .footer-b-r {
            float: right;
            color: #444032;
        }
    }
}
