.nav {
    ul {
        display: blockOld;
        height: 58px;
        margin-top: 14px;
        background: $default-yellow;
        @include clearfix();
        >li {
            float: left;
            >a {
                display: blockOld;
                width: 200px;
                height: 58px;
                line-height: 58px;
                background: $default-yellow;
                color: #fff;
                font-size: 16px;
                text-align: center;
                -webkit-transition: background 0.5s;
                -moz-transition: background 0.5s;
                -ms-transition: background 0.5s;
                -o-transition: background 0.5s;
                transition: background 0.5s;
                .icon {
                    display: none;
                    margin-bottom: 5px;
                    font-size: 20px;
                }
            }
            &:hover {
                >a {
                    background: $default-yellow-light;
                }
            }
            &.active {
                >a {
                    height: 72px;
                    line-height: 20px;
                    padding-top: 14px;
                    margin-top: -14px;
                    box-shadow: 0 -2px 8px 0 rgba(10, 3, 4, 0.1), 2px 0 8px 0 rgba(10, 3, 4, 0.1), -2px 0 8px 0 rgba(10, 3, 4, 0.1);
                    background: $default-yellow-light;
                    .icon {
                        display: blockOld;
                    }
                }
            }
        }
    }
}
