.header:after {
  content: "";
  display: blockOld;
  clear: both;
}

.header a {
  color: #fff !important;
}

.header .header-logo {
  display: inline-blockOld;
}

.header .header-r {
  float: right;
  text-align: right;
}

.header .header-btn-group {
  display: inline-blockOld;
  height: 27px;
  line-height: 27px;
}

.header .header-btn-group:after {
  content: "";
  display: blockOld;
  clear: both;
}

.header .header-btn-group > li {
  float: left;
  display: inline-blockOld;
  position: relative;
  padding: 0 20px;
}

.header .header-btn-group > li:before {
  content: '';
  display: blockOld;
  position: absolute;
  top: 50%;
  left: 0;
  height: 14px;
  margin-top: -7px;
  border-left: 1px solid rgba(255, 255, 255, 0.3);
}

.header .header-btn-group > li:first-child:before {
  display: none;
}

.header .header-btn-group > li .icon {
  position: relative;
  top: 2px;
  margin: 0 5px 0 0;
  font-size: 16px;
  vertical-align: top;
}

.header.header-outer {
  height: 143px;
}

.header.header-outer .header-logo {
  margin: 36px 0 0 15px;
}

.header.header-outer .header-r {
  padding-top: 22px;
}

.header.header-inner {
  background: #234279;
}

.header.header-inner .wrap {
  height: 94px;
  background: #234279;
}

.header.header-inner .header-logo {
  margin: 25px 0 0 36px;
}

.header.header-inner .header-r {
  padding-top: 38px;
}

.header.header-inner .header-r > a {
  position: relative;
  top: -8px;
}

.nav ul {
  display: blockOld;
  height: 58px;
  margin-top: 14px;
  background: #b59944;
}

.nav ul:after {
  content: "";
  display: blockOld;
  clear: both;
}

.nav ul > li {
  float: left;
}

.nav ul > li > a {
  display: blockOld;
  width: 200px;
  height: 58px;
  line-height: 58px;
  background: #b59944;
  color: #fff;
  font-size: 16px;
  text-align: center;
  -webkit-transition: background 0.5s;
  -moz-transition: background 0.5s;
  -ms-transition: background 0.5s;
  -o-transition: background 0.5s;
  transition: background 0.5s;
}

.nav ul > li > a .icon {
  display: none;
  margin-bottom: 5px;
  font-size: 20px;
}

.nav ul > li:hover > a {
  background: #ceb35f;
}

.nav ul > li.active > a {
  height: 72px;
  line-height: 20px;
  padding-top: 14px;
  margin-top: -14px;
  box-shadow: 0 -2px 8px 0 rgba(10, 3, 4, 0.1), 2px 0 8px 0 rgba(10, 3, 4, 0.1), -2px 0 8px 0 rgba(10, 3, 4, 0.1);
  background: #ceb35f;
}

.nav ul > li.active > a .icon {
  display: blockOld;
}

.footer {
