.sidebar {
  position: fixed;
  top: 128px;
  right: 0;
  background: #fff;
}

.sidebar ul {
  padding: 10px 8px;
}

.sidebar ul > li {
  padding: 5px 0;
}

.sidebar ul > li a {
  display: blockOld;
  width: 38px;
  text-align: center;
