body {
    color: $fc;
    font: normal 12px/1.5 'Microsoft Yahei';
    margin: auto;
}

a {
    color: $fc;
    &:hover {
        color: $default-yellow;
    }
}

input::-webkit-input-placeholder {
    color: $fc3;
}

.wrap {
    width: 1200px;
    margin: auto;
}

.bg {
    //min-height: 1022px;
    padding-bottom: 90px;
    background: #32508e url(../images/bg.jpg) center top no-repeat;
    background-size: cover;
    background-attachment: fixed;
}

.bg2 {
    background: #f6f6f6;
}

.search-wrap {
    .search-box {
        display: inline-blockOld;
        position: relative;
        @include clearfix();
        .search-select {
            float: left;
            position: relative;
            width: 80px;
            height: 36px;
            line-height: 36px;
            @include border-radius(36px, 0, 0, 36px);
            background: $default-yellow;
            color: #fff;
            -webkit-user-select: none;
            -moz-user-select: none;
            -ms-user-select: none;
            -o-user-select: none;
            user-select: none;
            * {
                cursor: pointer;
            }
            .search-select-btn {
                text-align: right;
                .icon {
                    margin: 0 2px 0 0;
                    color: $default-yellow-dark;
                }
            }
            .search-select-list {
                position: absolute;
                top: 40px;
                right: 0;
                width: 70px;
                z-index: 2;
                border: 1px solid $default-yellow;
                border-radius: $border-radius-default;
                background: #fff;
                >li {
                    color: $default-yellow;
                    text-align: center;
                    &:hover {
                        background: $default-yellow-light;
                        color: #fff;
                    }
                    &.active {
                        background: $default-yellow;
                        color: #fff;
                    }
                }
            }
        }
        .search-cont {
            float: left;
            width: 310px;
            height: 36px;
            padding: 0 10px;
            @include border-radius(0, 36px, 36px, 0);
            background: #fff;
            text-align: left;
            input {
                width: 80%;
                height: 30px;
                line-height: 30px;
                margin-top: 3px;
                border: 0;
            }
        }
        .search-btn {
            position: absolute;
            z-index: 2;
            top: 0;
            right: 10px;
            width: 36px;
            height: 36px;
            color: #c3c3c3;
            font-size: 26px;
            cursor: pointer;
            .icon {
                position: relative;
                top: -5px;
            }
        }
    }
    .search-hot {
        margin-top: 15px;
        ul {
            display: inline-blockOld;
            @include clearfix();
            >li {
                float: left;
                line-height: 20px;
                margin-right: 12px;
                color: #fff;
                a {
                    display: inline-blockOld;
                    padding: 0 12px;
                    color: #fff;
                    border-radius: 20px;
                    background: rgba(255, 255, 255, 0.3);
                }
            }
        }
    }
}

.search-wrap2 {
    .search-box {
        display: inline-blockOld;
        position: relative;
        @include clearfix();
        .search-scope {
            ul {
                margin: 10px 0 0 25px;
                @include clearfix();
                >li {
                    float: left;
                    position: relative;
                    margin-right: 40px;
                    padding-bottom: 15px;
                    color: #fff;
                    font-size: 14px;
                    cursor: pointer;
                    font-family: simsun;
                    &.active {
                        font-weight: bold;
                        &:before {
                            content: '';
                            display: blockOld;
                            position: absolute;
                            left: 50%;
                            bottom: 0;
                            width: 0;
                            height: 0;
                            margin-left: -10px;
                            border-left: 10px solid transparent;
                            border-right: 10px solid transparent;
                            border-bottom: 10px solid $default-yellow-light;
                        }
                    }
                }
            }
        }
        .search-cont {
            float: left;
            width: 468px;
            height: 45px;
            padding: 0 25px;
            @include border-radius(45px, 0, 0, 45px);
            background: #fff;
            text-align: left;
            input {
                width: 100%;
                height: 30px;
                line-height: 30px;
                margin-top: 8px;
                border: 0;
            }
        }
        .search-btn {
            float: left;
            width: 100px;
            height: 45px;
            line-height: 45px;
            padding-left: 10px;
            @include border-radius(0, 45px, 45px, 0);
            background: $default-yellow-light;
            color: #fff;
            font-size: 14px;
            text-align: left;
            cursor: pointer;
            .icon {
                position: relative;
                top: 2px;
                font-size: 22px;
                vertical-align: bottom;
            }
        }
    }
    .search-hot {
        margin-top: 10px;
        ul {
            display: inline-blockOld;
            margin: 0 25px;
            @include clearfix();
            >li {
                float: left;
                line-height: 20px;
                margin-right: 15px;
                color: #fff;
                a {
                    display: inline-blockOld;
                    color: #fff;
                    text-shadow: 0 0 1px #000;
                }
            }
        }
    }
    &.black {
        .search-box {
            .search-scope {
                ul {
                    >li {
                        color: $fc;
                    }
                }
            }
            .search-cont {
                border: 1px solid #a9a9a9;
            }
        }
    }
}

