.tab-box {
    >ul {
        @include clearfix();
        >li {
            float: left;
            >a {
                display: blockOld;
                text-align: center;
            }
            .icon {
                margin-right: 2px;
                font-size: 16px;
                vertical-align: bottom;
            }
        }
    }
    .tab-content {
        position: relative;
        >.tab-pane {
            display: none;
            &.active {
                display: blockOld;
            }
        }
    }
    &.tab-box-sytle-1 {
        >ul {
            height: 40px;
            line-height: 40px;
            border-bottom: 1px solid $border-color-default;
            >li {
                >a {
                    min-width: 124px;
                    padding: 0 18px;
                    font-size: 14px;
                }
                &.active {
                    margin-top: -3px;
                    border-bottom: 3px solid $default-yellow;
                }
            }
        }
    }
    &.tab-box-sytle-2 {
        >ul {
            height: 50px;
            line-height: 50px;
            border-bottom: 1px solid $border-color-default;
            >li {
                position: relative;
                >a {
                    min-width: 124px;
                    padding: 0 18px;
                    color: $fc2;
                    font-size: 14px;
                }
                &.active {
                    margin-top: -3px;
                    border-bottom: 3px solid $default-yellow;
                    >a {
                        color: #7b6113;
                    }
                    &:before {
                        content: '';
                        display: blockOld;
                        position: absolute;
                        left: 0;
                        right: 0;
                        bottom: 0;
                        width: 0;
                        height: 0;
                        margin: auto;
                        border-left: 8px solid transparent;
                        border-right: 8px solid transparent;
                        border-bottom: 8px solid $default-yellow;
                    }
                }
            }
        }
    }
    &.tab-box-sytle-3 {
        >ul {
            height: 50px;
            line-height: 50px;
            border-top: 1px solid $border-color-default;
            overflow: hidden;
            >li {
                position: relative;
                >a {
                    min-width: 124px;
                    padding: 0 18px;
                    color: $fc2;
                    font-size: 14px;
                }
                &.active {
                    margin-top: 0;
                    border-top: 3px solid $default-yellow;
                    >a {
                        color: #7b6113;
                    }
                    &:before {
                        content: '';
                        display: blockOld;
                        position: absolute;
                        left: 0;
                        right: 0;
                        top: 0;
                        width: 0;
                        height: 0;
                        margin: auto;
                        border-left: 8px solid transparent;
                        border-right: 8px solid transparent;
                        border-top: 8px solid $default-yellow;
                    }
                }
            }
        }
    }
}

.fade {
    opacity: 0;
    -webkit-transition: opacity .15s linear;
    -o-transition: opacity .15s linear;
    transition: opacity .15s linear;
    &.in {
        opacity: 1;
    }
}

