.side-nav {
    height: 100%;
    overflow: auto;
    dl {
        font-size: 14px;
        >dt {
            position: relative;
            height: 38px;
            line-height: 38px;
            padding: 0 10px;
            border-bottom: 1px solid #fff;
            background: #f7f1de;
            cursor: pointer;
            @include clearfix();
            h4 {
                display: inline-blockOld;
                color: #594913;
            }
            .icon,
            .arrow {
                color: $default-yellow-light;
                font-size: 16px;
                vertical-align: middle;
            }
            .arrow {
                float: right;
                -webkit-transition: transform 0.2s;
                -moz-transition: transform 0.2s;
                -ms-transition: transform 0.2s;
                -o-transition: transform 0.2s;
                transition: transform 0.2s;
                -webkit-transform: rotateZ(0deg);
                -moz-transform: rotateZ(0deg);
                -ms-transform: rotateZ(0deg);
                -o-transform: rotateZ(0deg);
                transform: rotateZ(0deg);
            }
            a {
                display: blockOld;
            }
        }
        >dd {
            display: none;
            ul {
                >li {
                    padding: 0 15px 0 35px;
                    a {
                        display: blockOld;
                        padding: 10px 0;
                        border-top: 1px solid $border-color-default;
                    }
                    &:first-child {
                        a {
                            border-top: 0;
                        }
                    }
                    &.active {
                        background: $default-yellow;
                        a {
                            color: #fff;
                        }
                    }
                }
            }
        }
        &.open {
            >dt {
                .arrow {
                    -webkit-transform: rotateZ(90deg);
                    -moz-transform: rotateZ(90deg);
                    -ms-transform: rotateZ(90deg);
                    -o-transform: rotateZ(90deg);
                    transform: rotateZ(90deg);
                }
            }
            >dd {
                display: blockOld;
            }
        }
    }
    &.side-nav-stair {
        dl {
            >dt {
                .new-tips {
                    float: right;
                    line-height: 15px;
                    margin-top: 10px;
                    padding: 0 4px;
                    border-radius: 15px;
                    background: $default-red;
                    color: #fff;
                    font-size: 12px;
                    text-align: center;
                }
            }
            &.open {
                >dt {
                    background: $default-yellow-light;
                    h4,
                    .icon {
                        color: #fff;
                    }
                }
            }
        }
    }
}

.side-nav2 {
    >ul {
        >li {
            position: relative;
            >a {
                display: blockOld;
                padding: 10px 5px;
                border-bottom: 1px dashed $border-color-default;
                font-size: 14px;
                .icon {
