            .menu {
                display: none;
                position: absolute;
                z-index: 2;
                top: 0;
                right: 260px;
                width: 272px;
                padding: 10px 0;
                border: 1px solid $border-color-default;
                background: #fff;
                &:before {
                    content: '';
                    position: absolute;
                    top: 0;
                    right: -11px;
                    width: 11px;
                    height: 45px;
                    background: #fff;
                }
                >li {
                    a {
                        display: blockOld;
                        padding: 5px 10px;
                        font-size: 12px;
                    }
                    &:hover {
