.filter-menu {
    border-bottom: 1px solid $border-color-default;
    dl {
        position: relative;
        margin-bottom: 15px;
        overflow: hidden;
        >dt {
            float: left;
            width: 50px;
            color: $fc2;
        }
        >dd {
            margin-left: 60px;
            a {
                float: left;
                position: relative;
                margin-right: 20px;
                color: $link-blue;
                &.active {
                    color: $default-red;
                    text-decoration: underline;
                }
            }
        }
        .more {
            float: right;
        }
    }
    &.filter-menu-set {
        padding: 15px 20px;
        border: 1px solid $border-color-default;
        background: #f6f6f6;
        dl {
            margin: 15px 0 0 0;
            overflow: visible;
            @include clearfix();
            &:first-child {
                margin: 0;
            }
            >dt {
                line-height: 26px;
                color: $fc;
                font-size: 14px;
            }
            >dd {
                a {
                    margin-right: 10px;
                    padding: 3px 6px;
                    border: 1px solid transparent;
                    border-radius: $border-radius-default;
                    color: $fc2;
                    .del {
                        display: none;
                        position: absolute;
                        top: -10px;
                        right: -10px;
                        color: $default-red;
                        font-size: 15px;
                    }
                    &:hover {
                        border-color: #c6c6c6;
                        .del {
                            display: blockOld;
                        }
                    }
                }
            }
            .filter-add {
                position: relative;
                .filter-add-box {
                    @include clearfix();
                    .text {
                        display: none;
                        float: right;
                        width: 130px;
                        height: 26px;
                        line-height: 24px;
                        padding: 0 10px;
                        border: 1px solid $border-color-default;
                        background: #fff;
                    }
                    .add {
                        float: right;
                        display: inline-blockOld;
                        height: 26px;
                        line-height: 26px;
                        padding: 0 8px;
                        background: $default-yellow;
                        color: #fff;
                        text-align: center;
                    }
                    &.active {
                        input {
                            display: blockOld;
                        }
                    }
                }
                .tips {
                    position: absolute;
                    bottom: -16px;
                    right: 0;
                    color: $fc3;
                    text-align: right;
                    -webkit-transform: scale(0.8);
                    -moz-transform: scale(0.8);
                    -ms-transform: scale(0.8);
                    -o-transform: scale(0.8);
                    transform: scale(0.8);
                }
            }
        }
    }
}

