.breadcrumb {
    padding: 10px 35px;
    border-bottom: 1px solid $border-color-default;
    background: #fff;
    ul {
        >li {
            display: inline-blockOld;
            font-size: 14px;
            &:before {
                content: '>';
                padding: 0 5px;
                font-family: simsun;
            }
            &:first-child {
                &:before {
                    display: none;
                }
            }
            a {
                color: $link-blue;
            }
        }
    }
}
