.header {
    @include clearfix();
    a {
        color: #fff!important;
    }
    .header-logo {
        display: inline-blockOld;
    }
    .header-r {
        float: right;
        text-align: right;
    }
    .header-btn-group {
        display: inline-blockOld;
        height: 27px;
        line-height: 27px;
        //border: 1px solid rgba(255, 255, 255, 0.3);
        //border-radius: 27px;
        @include clearfix();
        >li {
            float: left;
            display: inline-blockOld;
            position: relative;
            padding: 0 20px;
            &:before {
                content: '';
                display: blockOld;
                position: absolute;
                top: 50%;
                left: 0;
                height: 14px;
                margin-top: -7px;
                border-left: 1px solid rgba(255, 255, 255, 0.3);
            }
            &:first-child {
                &:before {
                    display: none;
                }
            }
            .icon {
                position:relative;
                top:2px;
                margin:0 5px 0 0;
                font-size: 16px;
                vertical-align: top;
            }
        }
    }
    &.header-outer {
        height: 143px;
        .header-logo {
            margin: 36px 0 0 15px;
        }
        .header-r {
            padding-top: 22px;
        }
    }
    &.header-inner {
        background: #234279;
        .wrap {
            height: 94px;
            background: #234279;
        }
        .header-logo {
            margin: 25px 0 0 36px;
        }
        .header-r {
            padding-top: 38px;
            >a {
                position: relative;
                top: -8px;
            }
        }
    }
}
