.clearfix {
    &:after {
        content: "";
        display: blockOld;
        clear: both;
    }
}

.fl {
    float: left!important;
}

.fr {
    float: right!important;
}

.hide {
    display: none!important;
}

.show {
    display: blockOld!important;
}

.ellipsis {
    overflow: hidden;
    word-wrap: break-word;
    text-overflow: ellipsis;
    white-space: nowrap;
}

.ellipsis2 {
    overflow: hidden;
    word-wrap: break-word;
    text-overflow: ellipsis;
    white-space: pre-wrap;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
}

.inline-blockOld {
    display: inline-blockOld;
}

.link-blue {
    color: $link-blue!important;
}

.fc-6 {
    color: $fc2!important;
}

.fc-9 {
    color: $fc3!important;
}

.fc-red {
    color: $default-red!important;
}

.fc-blue {
    color: $default-blue!important;
}

.fc-green {
    color: #42ac55!important;
}

.fc-de {
    color: #dedede!important;
}

.fz-12 {
    font-size: 12px!important;
}

.fz-14 {
    font-size: 14px!important;
}

.fz-16 {
    font-size: 16px!important;
}

.text-indent {
    text-indent: 2em!important;
}

.t-cen {
    text-align: center!important;
}

.align-r {
    text-align: right!important;
}

.line-dashed-b {
    border-bottom: 1px dashed $border-color-default;
}

.line-dashed-t {
    border-top: 1px dashed $border-color-default;
}

.border {
    border: 1px solid $border-color-default;
}

$direction:top right bottom left;
@for $i from 1 through 4 {
    @for $j from 1 through 6 {
        .margin-#{nth($direction,$i)}#{$j * 5} {
            margin-#{nth($direction,$i)}: $j * 5px!important;
        }
        .padding-#{nth($direction,$i)}#{$j * 5} {
            padding-#{nth($direction,$i)}: $j * 5px!important;
        }
    }
}
