.login-bg {
    background: url(../images/login-bg.jpg) center top no-repeat;
    background-size: cover;
}

.login-other {
    position: absolute;
    top: 20px;
    font-size: 14px;
    a {
        margin-left: 20px;
    }
}

.login-wrap {
    position: relative;
    .login-box {
        position: relative;
        width: 540px;
        height: 482px;
        margin: 5% auto auto auto;
        border: 1px solid #d2cddd;
        border-radius: 5px;
        box-shadow: 0 0 10px 0 #d2cddd;
        background: #fff;
        background: rgba(255, 255, 255, 0.8);
        text-align: center;
        .login-logo {
            display: blockOld;
            margin: 55px auto 60px auto;
        }
        .login-m {
            .item {
                width: 342px;
                height: 42px;
                padding: 0 10px;
                margin: 0 auto 15px auto;
                border: 1px solid #bcbcbc;
                background: #fff;
                @include clearfix();
                .icon {
                    float: left;
                    margin: -1px 10px 0 0;
                    color: $default-yellow-dark;
                    font-size: 26px;
                    vertical-align: sub;
                }
                input {
                    float: left;
                    width: 270px;
                    height: 40px;
                    line-height: 40px;
                    border: 0;
                    font-size: 14px;
                    text-align: left;
                }
            }
            .btn {
                display: blockOld;
                width: 176px;
                height: 34px;
                line-height: 34px;
                margin: 35px auto auto auto;
                border-radius: $border-radius-default;
                background: $default-yellow-light;
                color: #fff;
                font-size: 14px;
                &:hover {
                    background: $default-yellow;
                }
            }
        }
        .login-b {
            position: absolute;
            bottom: 0;
            left: 0;
            right: 0;
            width: 360px;
            margin: auto;
            padding: 36px 0 25px 0;
            border-top: 1px solid $border-color-default;
            ul {
                @include clearfix();
                >li {
                    float: left;
                    width: 50%;
                    text-align: center;
                    font-size: 14px;
                    a {
                        color: #68624e;
                    }
                    .icon {
                        font-size: 20px;
                        vertical-align: sub;
                    }
                }
            }
        }
    }
    .footer {
        // position: fixed;
        // bottom: 0;
        // left: 0;
        // right: 0;
    }
}
