$pagination_font_size:12px;
$pagination_font_color:$fc;
$pagination_active_font_color:#fff;
$pagination_bg:#fff;
$pagination_active_bg:$default-yellow-light;
$pagination_height:28px;
$pagination_radius:3px;
.pagination {
    overflow: hidden;
}

.pagination-page,
.pagination-info,
.pagination-size,
.pagination-jump {
    float: left;
    padding-right: 10px;
    padding-left: 0;
}

.pagination-page > li > a,
.pagination-size > select,
.pagination-group > input,
.pagination-group > button,
.pagination-info {
    font-size: $pagination_font_size;
    color: $pagination_font_color;
}


/* border */

.pagination-page > li > a,
.pagination-size > select,
.pagination-group > input,
.pagination-group > button {
    outline: none;
    @include user-select(none);
    border: 1px solid #e4e4e4;
}


/* button */

.pagination-page > li > a,
.pagination-group > button {
    cursor: pointer;
    background-color: $pagination_bg;
}

.pagination-page > li:hover > a,
.pagination-group > button:hover {
    background-color: darken($pagination_bg, 5%);
    border-color: #e4e4e4;
}

.pagination-page {
    margin: 0;
    >li {
        float: left;
        margin: 0 5px;
        list-style: none;
        >a {
            height: $pagination_height;
            line-height: $pagination_height - 2;
            min-width: $pagination_height;
            text-align: center;
            margin-left: -1px;
            display: inline-blockOld;
            padding: 0 6px;
            text-decoration: none;
            .page-arrow {
                position: relative;
                top: -1px;
                color: #0a5b78;
                font-weight: bold;
                font-family: simsun;
            }
        }
        &.active >a {
            color: $pagination_active_font_color;
            background-color: $pagination_active_bg;
            border-color: #fff;
        }
        &:first-child > a {
            margin-left: 0;
            border-top-left-radius: $pagination_radius;
            border-bottom-left-radius: $pagination_radius;
        }
        &:last-child > a {
            border-top-right-radius: $pagination_radius;
            border-bottom-right-radius: $pagination_radius;
        }
    }
}

.pagination-size > select {
    cursor: pointer;
    height: $pagination_height;
    padding: 0px 2px;
    border-radius: $pagination_radius;
}

.pagination-group {
    >input,
    >button {
        float: left;
    }
    > input {
        border-top-left-radius: $pagination_radius;
        border-bottom-left-radius: $pagination_radius;
        width: 40px;
        height: $pagination_height;
        padding: 0px;
        text-align: center;
    }
    > button {
        border-left: none;
        border-top-right-radius: $pagination_radius;
        border-bottom-right-radius: $pagination_radius;
        height: $pagination_height;
        padding: 0px 7px;
    }
}

.pagination-info {
    height: $pagination_height;
    line-height: $pagination_height;
}
